}

.cta {
  font-weight: 600;
  font-size: 0.7777777778rem;
  width: 13.6111111111rem;
  border-radius: 100px;
  background-color: #ff52bf;
  box-shadow: 0 8px 30px rgba(152, 157, 164, 0.477);
  border: 0 solid none;
  color: white;
  padding: 0.98em;
  transition: all 250ms;
}
.cta:hover {
  cursor: pointer;
  background-color: #ff99da;
}
.cta:active {
  transform: scale(0.95);
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.1);
}
@media (min-width: 40em) {
  .cta {
    font-size: 0.8888888889rem;
    width: 15.8333333333rem;
    padding: 1.05rem;
  }
}

