  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  margin: 0;
  padding: 0;
}

html, body {
  max-width: 100%;
  overflow-x: hidden;
}

.header {
  padding-block: 1.4444444444rem;
  padding-inline: 0.8888888889rem;
  margin-bottom: 5rem;
}
@media (min-width: 40em) {
  .header {
    padding-block: 3.0555555556rem;
    padding-inline: 4.1666666667rem;
  }
}
.header nav {
  justify-content: space-between;
}
.header__logo img {
  width: 5.8888888889rem;
  height: 1rem;
}
@media (min-width: 40em) {
  .header__logo img {
    width: 13.8888888889rem;
    height: 2.2222222222rem;
  }
}
.header__cta {
  font-size: 0.6666666667rem;
  font-weight: 600;
  width: 6.3888888889rem;
  border: 1px solid #ff52bf;
  border-radius: 5.5555555556rem;
  background-color: white;
  color: #ff52bf;
  padding: 0.4444444444rem;
  transition: color 350ms ease-in-out;
}
.header__cta:hover {
  cursor: pointer;
  color: white;
  background-color: #ff52bf;
  transition: all 250ms;
}
.header__cta:active {
  transform: scale(0.95);
}
@media (min-width: 40em) {
  .header__cta {
    font-size: 0.8888888889rem;
    width: 10.5555555556rem;
    padding: em(16);
  }
}

.primary {
